@import "./default.scss";
@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;400;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@100;400;700&display=swap");

html,
body {
  width: 100vw;
}

body,
h1,
h2,
div,
ul,
li,
p,
input,
select,
label {
  margin: 0;
  padding: 0;
}
body {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: $bgColor;
  color: white;
  font-family: "Roboto", "Noto Sans KR", "맑은 고딕", sans-serif;
}
button {
  padding: 0;
  border: none;
  background: transparent;
  cursor: pointer;
  font-family: "Roboto", "Noto Sans KR", "맑은 고딕", sans-serif;
}

.top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 8px 8px 0 0;
  box-sizing: border-box;
  padding: 7px 18px;
  button {
    font-weight: 700;
  }
  /* background-color와 color은 본인이 지정하시오 */
}

